<template>
  <transition name="fade">
    <header v-show="visibleHeader" class="animheader">
      <FlexBox justify="between" align="center" w="94%" h="100%" p="0 3%">
        <FlexBox
          justify="start"
          align="center"
          :w="currWidth < 544 ? '80%' : '33%'"
          h="100%"
        >
          <router-link class="nav-item" to="/">
            <h1>{{ t("Iam") }}</h1>
          </router-link>
        </FlexBox>
        <FlexBox
          justify="center"
          align="center"
          w="33%"
          h="100%"
          v-if="currWidth > 544"
        >
          <Navigation />
        </FlexBox>
        <FlexBox
          justify="end"
          align="center"
          w="33%"
          h="100%"
          v-if="currWidth > 544"
        >
          <FlexBox justify="end" align="center" w="auto" h="100%">
            <a
              href="https://www.instagram.com/vlada_kuldiaeva"
              class="footer-link"
            >
              <div class="insta-logo"></div>
            </a>
          </FlexBox>
          <LanguageBrick theme="dark" />
        </FlexBox>

        <FlexBox
          justify="end"
          align="center"
          w="20%"
          h="100%"
          v-if="currWidth <= 544"
        >
          <FlexBox justify="end" align="center">
            <div class="burger" @click="changeSideBarVisability"></div>
          </FlexBox>
        </FlexBox>
      </FlexBox>
    </header>
  </transition>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { mapGetters, mapMutations } from "vuex";
import FlexBox from "@/components/containers/FlexBox.vue";
import LanguageBrick from "@/components/bricks/LanguageBrick.vue";
import Navigation from "@/components/blocks/NavigationComponent.vue";
import { useI18n } from "vue-i18n";

export default defineComponent({
  name: "HeaderComponent",
  components: {
    FlexBox,
    LanguageBrick,
    Navigation,
  },
  setup() {
    const { t } = useI18n(); // call `useI18n`, and spread `t` from  `useI18n` returning
    return { t }; // return render context that included `t`
  },
  computed: {
    ...mapGetters({
      visibleHeader: "Status/visibleHeader",
      scrollY: "Status/currScrollY",
      currHeight: "Status/currHeight",
      currWidth: "Status/currWidth",
    }),
    isThemeSwitchShowed() {
      if (this.$route.name !== "home") {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    ...mapMutations({
      showheader: "Status/changeHeaderVisability",
      changeTopImageVisability: "Status/changeTopImageVisability",
      showTopImage: "Status/showTopImage",
      changeSideBarVisability: "Status/changeSideBarVisability",
      fixheader: "Status/showHeader",
    }),
  },
  watch: {
    scrollY(value) {
      if (this.$route.name !== "home") {
        if (this.visibleHeader === false) {
          this.fixheader();
        }
      } else {
        if (
          value > Math.round((this.currHeight * 3) / 5) &&
          !this.visibleHeader
        ) {
          this.showheader();
          this.changeTopImageVisability();
        } else if (
          value < Math.round((this.currHeight * 3) / 5) &&
          this.visibleHeader
        ) {
          this.showheader();
          this.showTopImage();
        }
      }
    },
  },
});
</script>

<style scoped lang="scss">
header {
  background-color: $black;
  height: max(64px, 6dvh);
}

svg {
  fill: silver;
}

svg:hover {
  fill: white;
}
h1,
.nav-item {
  font-family: "Playfair";
  color: silver;
  font-size: larger;
  font-weight: 500;
  text-decoration: none;
  cursor: pointer;
  user-select: none;
}

h1:hover {
  color: white;
}

.animheader {
  position: fixed;
  z-index: 3;
  width: 100vw;
  top: 0;
}
.fade-enter-active,
.fade-leave-active {
  transition: top 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
  top: calc((-1) * max(64px, 6dvh));
}
.footer-link {
  color: $white;
  text-decoration: none;
  line-height: 1em;
  font-weight: 600;
  margin-right: 10px;
}
.insta-logo {
  background-color: $white;
  background-size: contain;
  background-image: url("~@/assets/img/png/instagram.png");
  width: 30px;
  height: 30px;
  border-radius: 10px;
}
.container {
  height: 100%;
}
.burger {
  width: 6vw;
  height: 6vw;
  background-image: url("~@/assets/img/svg/burger.svg");
  background-repeat: no-repeat;
  background-size: 100%, auto;
}
.burger:hover {
  width: 6.4vw;
  height: 6.4vw;
  background-image: url("~@/assets/img/svg/burgerWhite.svg");
}

@media only screen and (min-width: 253px) and (max-width: 323px) {
  h1 {
    font-size: large;
  }
}
@media only screen and (min-width: 222px) and (max-width: 252px) {
  h1 {
    font-size: medium;
  }
}
@media only screen and (max-width: 221px) {
  h1 {
    font-size: 6vw;
  }
}
</style>
