<template>
  <SideBar />
  <Header />
  <main>
    <router-view />
  </main>
  <Footer />
</template>

<script setup lang="ts">
import { onMounted, onUnmounted } from "vue";
import { useStore } from "vuex";
import Footer from "@/components/blocks/FooterComponent.vue";
import Header from "@/components/blocks/HeaderComponent.vue";
import SideBar from "@/components/blocks/SideBarComponent.vue";

const store = useStore();

function changeMeasurments([height, width]: number[]) {
  store.commit("Status/changeCurrentMeasurment", [height, width]);
}

function handleScroll() {
  store.commit("Status/changeScrollMeasurment", window.scrollY);
}

function myEventHandler() {
  changeMeasurments([window.innerHeight, window.innerWidth]);
}

onMounted(() => {
  changeMeasurments([window.innerHeight, window.innerWidth]);
  window.addEventListener("resize", myEventHandler);
  window.addEventListener("scroll", handleScroll);
});
onUnmounted(() => {
  window.removeEventListener("resize", myEventHandler);
  window.removeEventListener("scroll", handleScroll);
});
</script>

