import { currState } from "@/shared/types";

export default {
  namespaced: true,
  actions: {
    changeSideBarVisability(context: any) {
      context.commit("changeSideBarVisability");
    },
  },
  mutations: {
    changeVisibilityLangList(state: currState) {
      state.visibleChooseLang = !state.visibleChooseLang;
    },
    changeCurrentLanguage(state: currState, newLang: string) {
      state.langvidge = newLang;
      state.visibleChooseLang = false;
    },
    changeTheme(state: currState) {
      state.lightTheme = !state.lightTheme;
    },
    setThemeLight(state: currState) {
      state.lightTheme = true;
    },
    changeCurrentMeasurment(state: currState, measurs: number[]) {
      state.currHeight = measurs[0];
      state.currWidth = measurs[1];
      if (measurs[1] > 544) {
        state.visibleSideBar = false;
      }
    },
    changeHeaderVisability(state: currState) {
      state.visibleHeader = !state.visibleHeader;
    },
    showHeader(state: currState) {
      state.visibleHeader = true;
    },
    changeTopImageVisability(state: currState) {
      state.visibleTopImage = !state.visibleTopImage;
    },
    changeExampleFontVisability(state: currState) {
      state.isFontsShowed = !state.isFontsShowed;
    },
    changeSideBarVisability(state: currState) {
      state.visibleSideBar = !state.visibleSideBar;
      state.visibleHeader = !state.visibleHeader;
    },
    showTopImage(state: currState) {
      state.visibleTopImage = true;
    },
    changeScrollMeasurment(state: currState, scroll: number) {
      state.currScrollY = scroll;
    },
  },
  state: () => ({
    currHeight: 0,
    currWidth: 0,
    currScrollY: 0,
    currPage: "home",
    lightTheme: true,
    langvidge: "ru",
    langvidges: ["ru", "en", "it"],
    visibleChooseLang: false,
    visibleHeader: false,
    visibleTopImage: false,
    visibleSideBar: false,
    isFontsShowed: false,
  }),
  getters: {
    langvidge: (state: currState) => state.langvidge,
    langvidges: (state: currState) => state.langvidges,
    visibleChooseLang: (state: currState) => state.visibleChooseLang,
    visibleHeader: (state: currState) => state.visibleHeader,
    visibleTopImager: (state: currState) => state.visibleTopImage,
    currScrollY: (state: currState) => state.currScrollY,
    currHeight: (state: currState) => state.currHeight,
    currWidth: (state: currState) => state.currWidth,
    visibleSideBar: (state: currState) => state.visibleSideBar,
    isFontsShowed: (state: currState) => state.isFontsShowed,
  },
};
