import { ActionContext } from "vuex";
import { user, resp, authState } from "@/shared/types";
import api from "@/shared/api";

export default {
  namespaced: true,
  actions: {
    async saltRequest(context: ActionContext<authState, authState>) {
      try {
        //const responce = await api.get("/login/secret");
        const responce = api.post("/login/secret", {
          web: "kuldiaeva",
          domen: "ru",
        });
        return responce;
      } catch (error: any) {
        if (error.response) {
          // Статус ответа выходит за пределы 2xx
          const { data, status, headers } = error.response;
          console.error(data);
        } else if (error.request) {
          // Отсутствует тело ответа
          console.error(error.request);
        } else {
          // Ошибка, связанная с неправильной настройкой запроса
          console.error(error.message);
        }
        // Другая ошибка
        console.error(error.config);
        // Подробная информация об ошибке
        console.error(error.toJSON());
      }
    },
    async authRequest(
      context: ActionContext<authState, authState>,
      user: user
    ) {
      try {
        api
          .post("/login/secret", {
            web: "kuldiaeva",
            domen: "ru",
          })
          .then((res) => {
            const token =
              btoa(res.data.token).slice(0, -1) +
              "." +
              btoa(user.username).slice(0, -1) +
              "." +
              btoa(user.password).slice(0, -1);

            api
              .post(
                "/login/check",
                {
                  user: user.username,
                },
                {
                  headers: {
                    Authorization: `token ${token}`,
                  },
                }
              )
              .then((res) => {
                if (res.data.severity === "token") {
                  // console.log(res.config.headers["Authorization"]);
                  localStorage.setItem("access-token", res.data["access"]);
                  localStorage.setItem(
                    "refresh-token",
                    String(res.config.headers["Authorization"])
                  );
                } else {
                  console.log(res.data.result);
                  return res.data.result;
                }
              });
          })
          .catch((err) => console.log(err));
      } catch (error: any) {
        if (error.response) {
          // Статус ответа выходит за пределы 2xx
          const { data, status, headers } = error.response;
          console.error(data);
        } else if (error.request) {
          // Отсутствует тело ответа
          console.error(error.request);
        } else {
          // Ошибка, связанная с неправильной настройкой запроса
          console.error(error.message);
        }
        // Другая ошибка
        console.error(error.config);
        // Подробная информация об ошибке
        console.error(error.toJSON());
      }
    },

    authLogout(context: ActionContext<authState, authState>) {
      return new Promise((resolve, reject) => {
        context.commit("authLogout");
        localStorage.removeItem("access-token"); // clear your user's token from localstorage
        resolve("out");
      });
    },
  },
  mutations: {
    authRequest(state: authState) {
      state.status = "loading";
    },
    authSuccess(state: authState, resp: resp) {
      state.status = "success";
      state.token = resp.token;
      state.hasLoadedOnce = true;
    },
    authError(state: authState) {
      state.status = "error";
      state.hasLoadedOnce = true;
    },
    authLogout(state: authState) {
      state.token = "";
    },
  },
  state: () => ({
    token: localStorage.getItem("user-token") || "",
    status: "",
    hasLoadedOnce: false,
  }),
  getters: {
    isAuthenticated: (state: authState) => !!state.token,
    authStatus: (state: authState) => state.status,
  },
};
