<template>
  <div class="languigeBtn">
    <div
      class="currFlag"
      :class="lang"
      @click="changeLang(lang, langvidge)"
    ></div>
  </div>
</template>

<script lang="ts">
import { mapMutations, mapGetters } from "vuex";
import i18n from "@/i18n";

export default {
  props: ["lang"],
  computed: {
    ...mapGetters({
      langvidge: "Status/langvidge",
    }),
  },
  methods: {
    ...mapMutations({
      change: "Status/changeCurrentLanguage",
      hide: "Status/changeVisibilityLangList",
    }),
    changeLang(lang: string, langvidge: string) {
      if (lang !== langvidge) {
        this.change(lang);
        i18n.global.locale.value = lang;
        this.hide;
      }
    },
  },
};
</script>

<style scoped lang="scss">
.languigeBtn {
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 4px;
  margin-right: 4px;
}
.currFlag {
  background-size: contain;
  background-repeat: no-repeat;

  width: 24px;
  height: 24px;
}
.currFlag:hover {
  width: 28px;
  height: 28px;
}
.ru {
  background-image: url("~@/assets/img/svg/ru.svg");
}

.en {
  background-image: url("~@/assets/img/svg/gb.svg");
}
.it {
  background-image: url("~@/assets/img/svg/it.svg");
}
</style>
