<template>
  <FlexBox w="auto">
    <LanguageBtn :lang="langvidge" @click="showIt" />
    <div :class="'otherLanguageBlock ' + theme" v-if="isShowed">
      <LanguageBtn v-for="lang in otherLanguages" :key="lang" :lang="lang" />
    </div>
  </FlexBox>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { mapGetters, mapMutations } from "vuex";
import FlexBox from "@/components/containers/FlexBox.vue";
import LanguageBtn from "@/components/bricks/LanguageBtn.vue";

export default defineComponent({
  name: "HeaderComponent",
  props: { theme: String },
  components: {
    FlexBox,
    LanguageBtn,
  },
  computed: {
    ...mapGetters({
      langvidge: "Status/langvidge",
      langvidges: "Status/langvidges",
      isShowed: "Status/visibleChooseLang",
    }),
    otherLanguages() {
      return this.langvidges.filter((lang: string) => lang !== this.langvidge);
    },
  },
  methods: {
    ...mapMutations({
      showIt: "Status/changeVisibilityLangList",
    }),
  },
  setup(props) {
    console.log(props.theme);
  },
});
</script>

<style scoped lang="scss">
.otherLanguageBlock {
  position: absolute;
  top: max(50px, 4dvh);
  padding-bottom: 4px;
  border-radius: 4px;
  z-index: 10;
}
.dark {
  background-color: $black;
}
.light {
  background-color: $white;
}
</style>
